import { useState, useEffect } from 'react';
import { Camera, CameraResultType } from '@capacitor/camera';
import { Link } from 'react-router-dom'
import { Preferences } from '@capacitor/preferences';

const Task: React.FC = () => {
    const [imgSrc, setImgSrc] = useState("")

    const getImgPref = async () => {
        const { value } = await Preferences.get({ key: 'CapacitorAppImage' });
        
        return setImgSrc(value || "")
    };

    useEffect(() => {
        getImgPref()
    }, [])

    const takePicture = async () => {
        const image = await Camera.getPhoto({
          quality: 90,
          allowEditing: true,
          resultType: CameraResultType.Uri
        });
      
        // image.webPath will contain a path that can be set as an image src.
        // You can access the original file using image.path, which can be
        // passed to the Filesystem API to read the raw data of the image,
        // if desired (or pass resultType: CameraResultType.Base64 to getPhoto)
        var imageUrl = image.webPath;
        console.log("Image URL: ", imageUrl)
      
        // Can be set to the src of an image now
        //   imageElement.src = imageUrl;
        setImgPref(imageUrl || "")
        setImgSrc(imageUrl || "")
    };

    const setImgPref = async (value: string) => {
        await Preferences.set({
          key: 'CapacitorAppImage',
          value,
        });
    };
      
    return (
        <>
            <h2>TASKS PAGE</h2>
            <button>
                <Link to="/" style={{textDecoration: "none"}}>Go Home</Link>
            </button>
            <br /><br />
            <button style={{ marginBottom: "2rem" }} onClick={takePicture}>
                Take Picture
            </button>
            {imgSrc && <img src={imgSrc} width="500" height="500"></img>}
        </>
    )
}

export default Task